<template>
  <div class="nature">
    <div class="title">
      <span class="title1">公司资质</span>
      <span class="title2">Company qualification</span>
    </div>
    <ul class="imgDiv">
      <li v-for="(item, index) in company" :key="index">
        <img :src="item.img_path" alt="" />
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: "nature",
  data() {
    return {};
  },
  props: ["company"]
};
</script>

<style lang="scss" type="text/scss" scoped>
.nature {
  .imgDiv {
    width: 79.68%;
    margin: 54px auto 0;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    li {
      width: 23%;
      img {
        width: 100%;
        margin-bottom: 20px;
      }
    }
  }
}
/* 移动端  */
@media all and (max-width: 768px) {
  .nature {
    .imgDiv {
      width: 95%;
      margin: 20px auto 0;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      li {
        width: 48.5%;
        img {
          width: 100%;
          margin-bottom: 20px;
        }
      }
    }
  }
}
</style>



