<template>
  <div class="teamDiv">
    <div class="title">
      <span class="title1">创始人介绍</span>
      <span class="title2">About Founder</span>
    </div>
    <div class="message" v-for="(item, index) in team" :key="index">
      <div class="lefts">
        <img :src="item.img_path" alt="" />
      </div>
      <div class="rights">
        <div class="tops">
          <span>{{ item.title }}</span>
          <span>{{ item.sub_title }}</span>
        </div>
        <ul>
          <li>
            <p v-html="item.content"></p>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "teamDiv",
  data() {
    return {};
  },
  props: ["team"]
};
</script>

<style lang="scss" type="text/scss" scoped>
.teamDiv {
  background: #f9f9f9;
  padding-bottom: 174px;
  .title {
    padding-top: 80px;
  }
  .message {
    width: 67.19%;
    // min-width: 1200px;
    margin: 107px auto 0;
    display: flex;
    justify-content: space-between;
    .lefts {
      width: 30%;
      img {
        width: 100%;
      }
    }

    .rights {
      width: 65%;
      display: flex;
      flex-direction: column;
      text-align: left;
      margin: 10px 0 20px 0;
      .tops {
        display: flex;
        flex-direction: column;
        span:nth-of-type(1) {
          font-size: 22px;
          font-weight: bold;
          color: #333333;
          margin-top: 10px;
        }
        span:nth-of-type(2) {
          color: #424242;
          font-size: 18px;
          margin-top: 20px;
        }
      }
      ul {
        /*min-width: 500px;*/
        margin-top: 50px;
        li {
          font-size: 14px;
          color: #757575;
          line-height: 40px;
        }
      }
    }
  }
}
/* 移动端  */
@media all and (max-width: 768px) {
  .teamDiv {
    background: #f9f9f9;
    padding-bottom: 0;
    .title {
      padding-top: 20px;
    }
    .message {
      width: 95%;
      // min-width: 1200px;
      margin: 20px auto 0;
      display: block;
      justify-content: space-between;
      .lefts {
        width: 100%;
        img {
          width: 100%;
        }
      }

      .rights {
        width: 100%;
        display: flex;
        flex-direction: column;
        text-align: left;
        margin: 10px 0 20px 0;
        .tops {
          display: flex;
          flex-direction: column;
          span:nth-of-type(1) {
            font-size: 22px;
            font-weight: bold;
            color: #333333;
            margin-top: 10px;
          }
          span:nth-of-type(2) {
            color: #424242;
            font-size: 18px;
            margin-top: 20px;
          }
        }
        ul {
          /*min-width: 500px;*/
          margin-top: 10px;
          li {
            font-size: 14px;
            color: #757575;
            line-height: 40px;
          }
        }
      }
    }
  }
}
</style>



