<template>
  <div class="aboutDiv">
    <div class="top" v-for="(item, index) in datas.nav_data" :key="index">
      <el-image :src="item.img_path" fit="cover"></el-image>
      <ul>
        <li v-for="(items, index) in item.top_list" :key="index">
          <span>{{ items.title }}</span>
          <span>{{ items.content }}</span>
        </li>
      </ul>
    </div>

    <clubDiv :circle="datas.circle_data"></clubDiv>

    <div class="content">
      <!--公司资质-->
      <nature :company="datas.company_data"></nature>

      <!--合作伙伴-->
      <partner :partner="datas.partner_data"></partner>

      <!--俱乐部活动赛事-->
      <Competition :active="datas.active_data"></Competition>

      <!--俱乐部项目资源类型-->
      <formDiv :resource="datas.resource_data"></formDiv>

      <!--俱乐部一站式服务-->
      <!-- <project :service="datas.service_data"></project> -->

      <!--核心团队-->
      <teamDiv :team="datas.team_data"></teamDiv>

      <!--底部-->
      <winDiv :foot="datas.foot_data"></winDiv>
    </div>
  </div>
</template>
<script>
import clubDiv from "@/components/aboutChilds/clubDiv";
import nature from "@/components/aboutChilds/nature";
import partner from "@/components/aboutChilds/partner";
import Competition from "@/components/aboutChilds/Competition";
import formDiv from "@/components/aboutChilds/formDiv";
import project from "@/components/aboutChilds/project";
import teamDiv from "@/components/aboutChilds/teamDiv";
import winDiv from "@/components/aboutChilds/winDiv";
import { GET_ABOUT } from "@/api/mall";
export default {
  name: "about",
  data() {
    return {
      datas: {}
    };
  },
  components: {
    clubDiv,
    nature,
    partner,
    Competition,
    formDiv,
    project,
    teamDiv,
    winDiv
  },
  methods: {
    listData() {
      GET_ABOUT({
        type: this.$store.state.isMobile
      })
        .then(res => {
          res.data.active_data = res.data.active_data.slice(0, 8);
          this.datas = res.data;
        })
        .catch(err => {});
    }
  },
  mounted() {
    this.listData();
  }
};
</script>
<style type="text/scss" lang="scss" scoped>
.aboutDiv {
  position: relative;
  .top {
    width: 100%;
    height: 1080px;
    position: relative;
    color: #fff;
    img {
      width: 100%;
      // height: 100%;
    }
    ul {
      position: absolute;
      left: 0;
      top: 230px;
      right: 0;
      margin: auto;
      height: 600px;
      width: 42.7%;
      display: flex;
      flex-wrap: wrap;
      li {
        width: 49%;
        height: 50%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        span:nth-of-type(1) {
          font-size: 28px;
          font-weight: bold;
        }
        span:nth-of-type(2) {
          padding: 20px 0 10px 0;
        }
        span {
          font-size: 16px;
        }
      }
      li:nth-of-type(-n + 2) {
        border-bottom: 1px solid rgba(255, 255, 255, 0.4);
      }
      li:nth-of-type(1),
      li:nth-of-type(3) {
        border-right: 1px solid rgba(255, 255, 255, 0.4);
      }
    }
  }
  .content {
    width: 100%;
    margin-top: 0;
  }
}
/* 移动端  */
@media all and (max-width: 768px) {
  .aboutDiv {
    position: relative;
    .top {
      width: 100%;
      height: auto;
      position: relative;
      color: #fff;
      img {
        width: 100%;
        // height: 100%;
      }
      ul {
        position: absolute;
        left: 0;
        top: 230px;
        right: 0;
        margin: auto;
        height: 600px;
        width: 42.7%;
        display: flex;
        flex-wrap: wrap;
        li {
          width: 49%;
          height: 50%;
          display: flex;
          flex-direction: column;
          justify-content: center;
          span:nth-of-type(1) {
            font-size: 28px;
            font-weight: bold;
          }
          span:nth-of-type(2) {
            padding: 20px 0 10px 0;
          }
          span {
            font-size: 16px;
          }
        }
        li:nth-of-type(-n + 2) {
          border-bottom: 1px solid rgba(255, 255, 255, 0.4);
        }
        li:nth-of-type(1),
        li:nth-of-type(3) {
          border-right: 1px solid rgba(255, 255, 255, 0.4);
        }
      }
    }
    .content {
      width: 100%;
      margin-top: 50px;
    }
  }
}
</style>



