<template>
  <div class="Competition">
    <div class="title">
      <span class="title1">俱乐部活动赛事</span>
      <span class="title2">Event events</span>
    </div>
    <ul class="imgDiv">
      <li
        v-for="(item, index) in active"
        :key="index"
        @click="toDetails(item.id)"
      >
        <div class="comp-img">
          <img :src="item.img_list_path" alt="" />
        </div>
        <div class="comp-text">
          <div :title="item.title" class="omit">{{ item.title }}</div>
          <div :title="item.sub_title" class="omit">{{ item.sub_title }}</div>
        </div>
      </li>
    </ul>

    <div class="btn">
      <div @click="toList">查看更多</div>
    </div>
  </div>
</template>
<script>
export default {
  name: "Competition",
  data() {
    return {};
  },
  props: ["active"],
  methods: {
    toList() {
      this.$router.push({
        path: "/activityLists"
      });
    },
    toDetails(e) {
      console.log(111, e);
      this.$router.push({
        path: "/activityDetails",
        query: {
          id: e
        }
      });
    }
  }
};
</script>

<style lang="scss" type="text/scss" scoped>
.Competition {
  margin-top: 69px;
  background: #f9f9f9;
  .title {
    padding-top: 98px;
  }
  .imgDiv {
    width: 79.68%;
    margin: 54px auto 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    li {
      width: 24%;
      color: #000000;
      margin-bottom: 20px;
      transition: all 0.6s;
      cursor: pointer;
      .comp-img {
        overflow: hidden;
        img {
          width: 100%;
          transition: all 0.6s;
        }
      }
      .comp-text {
        display: flex;
        flex-direction: column;
        text-align: left;
        padding-left: 19px;
        background: #fff;
        div:nth-of-type(1) {
          font-weight: bold;
          font-size: 18px;
          margin: 19px 0 10px 0;
        }
        div:nth-of-type(2) {
          font-size: 12px;
          margin-bottom: 20px;
        }
      }
      &:hover {
        box-shadow: 0px 10px 13px 0px rgba(0, 0, 0, 0.12);
        img {
          transform: scale(1.1);
        }
      }
      &:last-child:nth-child(4n - 1) {
        margin-right: calc(24% + 4% / 3);
      }
      /* 如果最后一行是2个元素 */
      &:last-child:nth-child(4n - 2) {
        margin-right: calc(48% + 8% / 3);
      }
    }
  }
  .btn {
    padding: 40px 0 104px 0;
    cursor: pointer;
    div {
      width: 6.6%;
      min-width: 120px;
      height: 40px;
      border: 1px solid rgba(153, 153, 153, 1);
      border-radius: 6px;
      line-height: 40px;
      text-align: center;
      font-size: 14px;
      color: #999999;
      margin: 0 auto;
    }
  }
}
/* 移动端  */
@media all and (max-width: 768px) {
  .Competition {
    margin-top: 0;
    background: #f9f9f9;
    .title {
      padding-top: 20px;
    }
    .imgDiv {
      width: 95%;
      margin: 20px auto 0;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      li {
        width: 48.5%;
        color: #000000;
        margin-bottom: 20px;
        transition: all 0.6s;
        cursor: pointer;
        .comp-img {
          overflow: hidden;
          img {
            width: 100%;
            transition: all 0.6s;
          }
        }
        .comp-text {
          display: flex;
          flex-direction: column;
          text-align: left;
          background: #fff;
          div:nth-of-type(1) {
            font-weight: bold;
            font-size: 18px;
            line-height: 25px;
            padding: 1px 0 0 0;
          }
          div:nth-of-type(2) {
            font-size: 12px;
            margin-bottom: 20px;
            line-height: 15px;
          }
        }
        &:hover {
          box-shadow: 0px 10px 13px 0px rgba(0, 0, 0, 0.12);
          img {
            transform: scale(1.1);
          }
        }
        &:last-child:nth-child(4n - 1) {
          margin-right: calc(24% + 4% / 3);
        }
        /* 如果最后一行是2个元素 */
        &:last-child:nth-child(4n - 2) {
          margin-right: calc(48% + 8% / 3);
        }
      }
    }
    .btn {
      padding: 0 0 20px 0;
      cursor: pointer;
      div {
        width: 6.6%;
        min-width: 120px;
        height: 40px;
        border: 1px solid rgba(153, 153, 153, 1);
        border-radius: 6px;
        line-height: 40px;
        text-align: center;
        font-size: 14px;
        color: #999999;
        margin: 0 auto;
      }
    }
  }
}
</style>



