<template>
  <div class="formDiv" :style="imgBg">
    <div class="title">
      <span class="title1">俱乐部项目资源类型</span>
      <span class="title2">Project resources</span>
    </div>
    <ul class="imgDiv">
      <li v-for="(item, index) in resource" :key="index">
        <img :src="item.img_path" alt="" />
        <span>{{ item.title }}</span>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: "formDiv",
  data() {
    return {
      imgBg: {
        backgroundImage: "url('/static/images/aboutBg.png')",
        backgroundRepeat: "no-repeat"
      }
    };
  },
  props: ["resource"]
};
</script>

<style lang="scss" type="text/scss" scoped>
.formDiv {
  width: 100%;
  height: 1080px;
  .title {
    .title1 {
      margin-top: 97px;
      color: #fff;
    }
    .title2 {
      color: #fff;
    }
  }
  .imgDiv {
    width: 72.91%;
    // min-width: 1100px;
    margin: 128px auto 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    color: #fff;
    font-size: 18px;
    font-weight: bold;
    li {
      width: 24.93%;
      height: 300px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      border-right: 1px solid rgba(255, 255, 255, 0.4);
      img {
        width: 18.18%;
        margin-bottom: 35px;
      }
    }
    li:nth-of-type(-n + 4) {
      border-bottom: 1px solid rgba(255, 255, 255, 0.4);
    }
    li:nth-of-type(4),
    li:nth-of-type(8) {
      border-right: none;
    }
  }
}
/* 移动端  */
@media all and (max-width: 768px) {
  .formDiv {
    width: 100%;
    height: auto;
    .title {
      .title1 {
        margin-top: 20px;
        color: #fff;
      }
      .title2 {
        color: #ffffff;
      }
    }
    .imgDiv {
      width: 95%;
      // min-width: 1100px;
      margin: 20px auto 50px;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      font-weight: bold;
      li {
        width: 49.5%;
        height: auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        border-bottom: 1px solid rgba(255, 255, 255, 0.4);
        // background: #474849;
        margin-bottom: 0;
        // border-radius: 8px;
        border-right: none;
        padding: 20px 0;
        span {
          font-size: 18px;
          color: #fff;
          font-weight: 500;
        }
        img {
          width: 20%;
          margin-bottom: 20px;
        }
      }
      li:nth-of-type(odd) {
        border-right: 1px solid rgba(255, 255, 255, 0.4);
      }
      li:nth-of-type(-n + 4) {
        border-bottom: 1px solid rgba(255, 255, 255, 0.4);
      }
      li:nth-of-type(4),
      li:nth-of-type(8) {
        border-right: none;
      }
    }
  }
}
</style>



