<template>
  <div class="partner">
    <div class="title">
      <span class="title1">合作伙伴</span>
      <span class="title2">Our Customers</span>
    </div>
    <ul class="imgDiv">
      <li v-for="(item, index) in partner" :key="index">
        <div class="Before">
          <el-image :src="item.img_path_face" alt="" />
        </div>
        <div class="After">
          <el-image :src="item.img_path_back" alt="" />
        </div>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: "partner",
  data() {
    return {};
  },
  props: ["partner"]
};
</script>

<style lang="scss" type="text/scss" scoped>
.partner {
  margin-top: 80px;
  .imgDiv {
    width: 79.68%;
    margin: 63px auto 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    li {
      width: 18%;
      height: 130px;
      margin-bottom: 69px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      background: #f9fbfc;
      cursor: pointer;
      position: relative;
      .Before {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background-repeat: no-repeat;
        backface-visibility: hidden;
        transition: 1.2s;
        display: flex;
        justify-content: center;
        align-items: center;
      }
      .After {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        transform: rotateY(-180deg);
        backface-visibility: hidden;
        transition: 1.2s;
      }
      &:hover .Before {
        transform: rotateY(180deg);
      }
      &:hover .After {
        transform: rotateY(0deg);
      }

      img {
        width: 78.125%;
      }
      &:last-child:nth-child(5n - 1) {
        margin-right: calc(18% + 10% / 4);
      }
      &:last-child:nth-child(5n - 2) {
        margin-right: calc(36% + 20% / 4);
      }
      /* 如果最后一行是2个元素 */
      &:last-child:nth-child(5n - 3) {
        margin-right: calc(54% + 30% / 4);
      }
    }
  }
}
/* 移动端  */
@media all and (max-width: 768px) {
  .partner {
    margin-top: 20px;
    .imgDiv {
      width: 95%;
      margin: 20px auto 0;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      li {
        width: 48.5%;
        height: 120px;
        margin-bottom: 20px;
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        justify-content: space-between;
        background: #f9fbfc;
        cursor: pointer;
        position: relative;
        .Before {
          width: 100%;
          height: 100%;
          position: absolute;
          top: 0;
          left: 0;
          background-repeat: no-repeat;
          backface-visibility: hidden;
          transition: 1.2s;
          display: flex;
          justify-content: center;
          align-items: center;
        }
        .After {
          width: 100%;
          height: 100%;
          position: absolute;
          top: 0;
          left: 0;
          display: flex;
          justify-content: center;
          align-items: center;
          transform: rotateY(-180deg);
          backface-visibility: hidden;
          transition: 1.2s;
        }
        &:hover .Before {
          transform: rotateY(180deg);
        }
        &:hover .After {
          transform: rotateY(0deg);
        }

        ::v-deep .el-image__inner {
          width: 60%;
        }
        &:last-child:nth-child(5n - 1) {
          margin-right: calc(18% + 10% / 4);
        }
        &:last-child:nth-child(5n - 2) {
          margin-right: calc(36% + 20% / 4);
        }
        /* 如果最后一行是2个元素 */
        &:last-child:nth-child(5n - 3) {
          margin-right: calc(54% + 30% / 4);
        }
      }
    }
  }
}
</style>



