<template>
  <div>
    <ul class="clubDiv" v-for="(item, index) in circle" :key="index">
      <li>{{ item.title }}</li>
      <li v-html="item.content"></li>
    </ul>
  </div>
</template>
<script>
export default {
  name: "clubDiv",
  data() {
    return {};
  },
  props: ["circle"]
};
</script>

<style lang="scss" type="text/scss" scoped>
.clubDiv {
  width: 79.68%;
  background: #fff;
  position: relative;
  left: 0;
  right: 0;
  margin: auto;
  top: -100px;
  box-shadow: 0px 3px 35px 0px rgba(0, 0, 0, 0.03);
  border-radius: 20px;
  li:nth-of-type(1) {
    font-size: 42px;
    color: #010101;
    font-weight: bold;
    padding-top: 132px;
  }
  li:nth-of-type(2) {
    width: 81.96%;
    // min-width: 700px;
    margin: 0 auto;
    padding: 50px 0 100px 0;
    font-size: 16px;
    line-height: 44px;
  }
}
/* 移动端  */
@media all and (max-width: 768px) {
  .clubDiv {
    width: 95%;
    background: #fff;
    position: relative;
    left: 0;
    right: 0;
    margin: auto;
    top: 20px;
    box-shadow: 0px 3px 35px 0px rgba(0, 0, 0, 0.03);
    border-radius: 20px;
    li:nth-of-type(1) {
      font-size: 28px;
      color: #010101;
      font-weight: bold;
      padding-top: 0;
    }
    li:nth-of-type(2) {
      width: 100%;
      // min-width: 700px;
      margin: 0 auto;
      padding: 20px 0;
      font-size: 16px;
      line-height: 44px;
    }
  }
}
</style>



