<template>
  <div>
    <div v-for="(item, index) in foot" :key="index" class="winDiv">
      <span class="iconfont" style="color: #89979D">&#xe6d9;</span>
      <span>{{ item.title }}</span>
      <span>{{ item.sub_title }}</span>
    </div>
  </div>
</template>
<script>
export default {
  name: "winDiv",
  data() {
    return {};
  },
  props: ["foot"]
};
</script>

<style lang="scss" type="text/scss" scoped>
.winDiv {
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 80px 0 80px 0;
  color: #89979d;
  span:nth-of-type(1) {
    font-size: 57px;
  }
  span:nth-of-type(2) {
    font-size: 37px;
    margin: 25px 0 13px 0;
  }
  span:nth-of-type(3) {
    font-size: 18px;
  }
}
/* 移动端  */
@media all and (max-width: 768px) {
  .winDiv {
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 80px 0;
    color: #89979d;
    span:nth-of-type(1) {
      font-size: 32px;
    }
    span:nth-of-type(2) {
      font-size: 28px;
      margin: 20px 0;
    }
    span:nth-of-type(3) {
      font-size: 18px;
    }
  }
}
</style>



