<template>
  <div class="project">
    <div class="title">
      <span class="title1">俱乐部一站式服务</span>
      <span class="title2">One stop service</span>
    </div>
    <ul class="imgDiv">
      <li v-for="(item,index) in service"
          :key="index">
        <img :src="item.img_path"
             alt="">
        <div>{{item.title}}</div>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: 'project',
  data() {
    return {};
  },
  props: ['service'],
};
</script>

<style lang="scss" type="text/scss" scoped>
.project {
  margin: 93px 0 178px 0;
  .imgDiv {
    width: 79.68%;
    margin: 104px auto 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    li {
      width: 32.5%;
      display: flex;
      flex-direction: column;
      margin-bottom: 15px;
      img {
        width: 100%;
      }
      div {
        text-align: center;
        color: #fff;
        background: #111111;
        font-size: 18px;
        font-weight: bold;
        padding: 20px;
      }
      &:last-child:nth-child(3n - 1) {
        margin-right: calc(32.5% + 3.5% / 3);
      }
    }
  }
}
</style>



